<!-- svelte-ignore missing-declaration -->
<style type="text/scss" global >
/*@import "./styles.bulma.scss";*/

</style>
<script>
import { ShadowRoot } from './libraries/ShadowRoot.js';
import Styles from './Styles.svelte';
import CorePlans from './plans/CorePlans.svelte';
import { Connect3Pod } from './libraries/Connect3Pod';
let user = tripod_plans?.user ?? {};
let affiliate;
let host = tripod_plans.host;

let fetched_user = false;
user.referred_by = user.referred_by ?? "";
if(user.referred_by === "" || typeof user.email === "undefined"){
    fetched_user = true;
}

if(fetched_user === false){
    const user_public_key_reversed = user.public_key.split("").reverse().join("");
    const affiliate_public_key_reversed = user.referred_by.split("").reverse().join("");
    Connect3Pod(`${host}public/api/v1/affiliates/referredByData`, {
        data: {
            public_key_user: user_public_key_reversed,
            public_key_affiliate: affiliate_public_key_reversed,
        },
        ready: (data)=>{
            affiliate = JSON.parse(data[0]);
            fetched_user = true;
        }
    })
}

</script>
<div class="svelte-module sv">
    {#if fetched_user === true}
        <Styles />
        <CorePlans affiliate={affiliate} user={user} />
    {/if}
</div>